import React from 'react';
import { LinkProps, Link as L } from 'react-router-dom';
import * as regexes from './regexes';

export function Link(props: LinkProps & React.RefAttributes<HTMLAnchorElement> & {
	to?: string | undefined
}) {

	if (props.to && !regexes.link.test(props.to) && !props.to.startsWith('#')) {

		return <L {...props} href={props.to}>
			{props.children}
		</L>;

	} else {

		return <a href={props.to || undefined} rel='noopener noreferrer' target='_blank' {...props}>
			{props.children}
		</a>;
		
	}
}